      .home__options-container__option
        img(src="../assets/img/HomePage/mysql_group_compressed.png")
      .home__options-container__option
        img(src="../assets/img/HomePage/python_group_compressed.png")
</template>

<script>
  export default {
    data () {
      return {
        msg: 'Hello'
      }
    }
  }
</script>

<style lang="sass">
  .home
    position: absolute
    height: 100%
    width: 100%
    box-sizing: border-box
    &__title-container
      position: relative
      top: 25%
      max-width: 400px
      height: 30px
      margin: 0px auto
      text-align: center
      letter-spacing: .05em
      &__main-title
        font-family: 'Special Elite', cursive
        color: #fdfcf9
        font-size: 36px
      &__sub-title
        margin-top: 14px
        font-family: 'Averia Gruesa Libre', cursive
        font-size: 18px
        color: #e9e9e9
    &__options-container
      position: relative
      top: 55%
      box-sizing: border-box
      max-width: 600px
      margin: 0px auto
      display: flex
      align-items: center
      &__option
        box-sizing: border-box
        flex: 1 1 auto
        opacity: 0.8
        transition: opacity 350ms linear
        &:hover
          opacity: 1.0
          transition: opacity 180ms linear
          cursor: pointer
        img
          width: 100%
  .home-test
    width: 200px
    height: 100px
    background-color: white
    margin: 0px auto
    position: relative
</style>



