// import VueCookies from 'vue-cookies'
import axios from 'axios'

export default {
  namespaced: true,
  state: {
    user: {
      firstName: '',
      lastName: '',
      username: '',
      email: ''
    },
    tokenKey: ''
  },
  getters: {
    userAnnouncement: (state, getters, rootState) => phrase => {
      return state.name.toUpperCase() + (phrase ? ' says ' + phrase : '')
    }
  },
  mutations: {
    setTokenKey (state, payload) {
      state.tokenKey = payload.key
    }
  },
  actions: {
    async login ({commit, dispatch}, payload) {
      const response = await axios.post('rest-auth/login/', {
        username: payload.username,
        password: payload.password
      })
      console.log(response)
      // return
      // VueCookies.set('tokenKey', data.key)
      // commit('setTokenKey', data)
      // dispatch('getName')
    }
  }
}



