<template lang="pug">
  .nav
    .nav__container
      .nav__container__page-up
        span(v-if="backActivated") Back
      .nav__container__quote “If you have dreams it is your responsibility to make them happen.” - Bel Pesce
      .nav__container__user(style="cursor: pointer;", @click="$emit('toggle-visibility')") LOGIN | REGISTER
</template>

<script>
  export default {
    data () {
      return {
        backActivated: false
      }
    }
  }
</script>

<style lang="sass" scoped>
  @import '../assets/sass/vars'
  .nav
    z-index: 10
    position: fixed
    width: 100%
    height: 30px
    background-color: rgba(192,192,192,0.3)
    &__container
      position: relative
      margin: 0 auto
      max-width: 1200px
      padding: 0 10px
      height: 100%
      display: flex
      align-items: center
      & > div
        box-sizing: border-box
        flex: 1 1 auto
        +averia-font()
        &[class$=quote]
          flex: 3 1 auto
          +text-normal-white(0.8)
          font-size: 14px
          text-align: center
          padding: 0 9px
          letter-spacing: .02em
          @media (max-width: 900px)
            font-size: 13px
          @media (max-width: 767px)
            display: none
        &[class$=user], &[class$=page-up]
          min-width: 100px
        &[class$=user]
          font-size: 12px
          text-align: right
          +text-normal-white(1.0)
</style>



