<template lang="pug">
  #app
    app-nav(@toggle-visibility="toggleAuth")
    router-view
    login-register(:show="authVisibility")
</template>

<script>
  import {mapGetters, mapActions, mapMutations} from 'vuex'
  import AppNav from '@/components/AppNav'
  import LoginRegister from '@/components/LoginRegister'

  export default {
    name: 'app',
    components: {
      AppNav,
      LoginRegister
    },
    data () {
      return {
        authVisibility: false
      }
    },
    computed: {
      ...mapGetters({
        fullName: 'fullName'
      })
    },
    methods: {
      toggleAuth () {
        this.authVisibility = !this.authVisibility
      },
      ...mapActions({
        getName: 'getName'
      }),
      ...mapMutations({
        setKey: 'setKey'
      })
    },
    created () {
      // FIXME: this goes somewhere else
      const loginCookieSet = this.$cookies.isKey('tokenKey')
      console.log(loginCookieSet)
      if (loginCookieSet) {
        this.setKey({key: this.$cookies.get('tokenKey')})
        this.getName()
      }
    }
  }
</script>

<style lang="sass">
  @import 'assets/sass/app'

  #app
    min-height: 100%

  li
    color: red
</style>



