<template lang="pug">
  modal-window(v-if="show")
    .content
      .headings
        .headings__title LOGIN
        .headings__sub-title Enter email to begin.
      .form
        input-field(
          id="email-input",
          name="email",
          @update="update('username', $event)",
          @enter="login(user)"
        )
        input-field(
          id="password-input",
          name="password",
          :password="true",
          @update="update('password', $event)",
          @enter="login(user)"
        )
        img(src="../assets/img/LoginRegister/right-arrow.png", class="login-arrow", @click="login(user)")
      .headings(style="margin-top: 35px;")
        .headings__title REGISTER
        .headings__sub-title Select a method to register.

</template>

<script>
  import ModalWindow from '@/blocks/ModalWindow'
  import InputField from '@/blocks/InputField'
  import {mapActions} from 'vuex'

  export default {
    components: {
      ModalWindow,
      InputField
    },
    props: {
      show: {required: true}
    },
    data () {
      return {
        user: {
          username: '',
          password: ''
        }
      }
    },
    methods: {
      ...mapActions({
        login: 'user/login'
      }),
      update (...args) {
        // args[0] is the field name (eg. username)
        // args[1] is the actual value from the field
        this.$set(this.user, args[0], args[1])
      }
    }
  }
</script>

<style lang="sass">
  @import '../assets/sass/vars'

  .headings
    margin-bottom: 19px
    +averia-font
    .headings__title
      +text-normal-white(1.0)
      font-size: 20px
      margin-bottom: 5px
    .headings__sub-title
      +text-normal-white(0.8)
      font-size: 12px
  .form
    .login-arrow
      margin-top: 20px
      cursor: pointer
      opacity: 0.85
      transition: opacity 150ms linear
      &:hover
        opacity: 1
        transition: opacity 150ms linear

</style>



